@use "base/variables" as v;
@use "base/mixins" as m;

.header {
  position: relative;
  z-index: 10;
  width: 100%;
  background: v.$negro;

  @include m.tablet {
    position: absolute;
    backdrop-filter: blur(1px);
    background: rgba(171, 128, 128, 0.15);

    &.fijo {
      position: fixed;
      width: 100%;
      background-color: v.$negro;
    }
  }
  .contenido-logo {
    display: flex;
    justify-content: center;
    text-align: center;
    margin: 0;
  }
  .contenido-header {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    @include m.tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: unset;
      width: unset;
    }
  }
}

.logo {
  width: 15rem;
  height: 7rem;
  text-align: center;
  margin: 0;

  @include m.tablet {
    margin: 0;
    padding-right: 1rem;
    padding-bottom: 0;
  }
}

.navegacion-principal {
  display: none;
  @include m.tablet {
    display: flex;
    gap: 2.5rem;
    margin-right: 2rem;
  }
  a {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: v.$blanco;
    font-size: 1.5rem;
    margin: 1rem auto;
    width: 16rem;

    @include m.tablet {
      display: block;
      text-align: center;
      color: v.$blanco;
      font-size: 1.5rem;
      margin-bottom: 1rem;
      transition: 0.5s;
      width: auto;
    }
    &:hover {
      color: v.$sorbus500;
    }
  }
}

.hamburger {
  display: block; // Visible solo en pantallas menores a 768px
  cursor: pointer;
  position: absolute;
  right: 1rem;
  top: 2rem;

  @include m.tablet {
    display: none;
  }

  svg {
    height: 3em;
    transition: transform 600ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .line {
    fill: none;
    stroke: v.$blanco;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-width: 3;
    transition: stroke-dasharray 600ms, stroke-dashoffset 600ms;
  }

  .line-top-bottom {
    stroke-dasharray: 12 63;
  }

  input {
    display: none;

    &:checked + svg .line-top-bottom {
      stroke-dasharray: 20 300;
      stroke-dashoffset: -32.42;
      stroke: v.$sorbus600;
    }

    &:checked + svg {
      transform: rotate(-45deg);
    }
  }
}
