@use "base/variables" as v;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Work Sans", sans-serif;
  font-size: 1.6rem;
  &.fijar-body {
    overflow: hidden;
  }
}

p {
  font-size: 2rem;
  color: v.$negro;
}

.contenedor {
  width: 95%;
  max-width: 150rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  height: auto;
}
