@use "base/mixins" as m;
@use "base/variables" as v;

.ambiente-container {
  margin-top: 2rem;
  width: 100%;

  .imagen {
    position: relative;
    width: 100%;
    overflow: hidden;
    height: 25rem;

    @include m.telefono {
      height: 35rem;
    }
    @include m.tablet {
      height: 50rem; 
    }

    @include m.desktop {
      height: 60rem; 
    }

    img {
      width: 100%;
      height: 26rem; 
      object-fit: cover; 
      object-position: left center; 

      @include m.telefono {
        height: 36rem;
      }

      @include m.tablet {
        height: 51rem; 
      }
     
      @include m.desktop {
        height: auto; 
      }
    }

    .overlay {
      position: absolute;
      height: 100%;
      width: 100%;
      background: linear-gradient(
        0deg,
        rgba(0, 0, 0, 0.65) 100%,
        rgba(0, 0, 0, 0.65) 100%
      );
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: center;

      p {
        color: v.$blanco;
        font-size: 1rem;
        font-weight: 400;
        margin: 0 2rem;
        padding: 1.3rem;
        text-align: justify;

        @include m.telefono {
          font-size: 1.5rem;
        }

        @include m.tablet {
          font-size: 2.3rem;
        }
      }
    }
  }
}

.logos {
  margin-top: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 0.5rem;
  .certificado {
    width: auto;
    height: 3.5rem;
    margin: 0 5px;
  }

  @include m.telefono {
    justify-content: space-around;
    font-size: 2rem;

    .certificado {
      width: auto;
      height: 5rem;
      margin: 0 5px;
    }
  }
  @include m.tablet {
    overflow: hidden;
    padding: 1.5rem 0;
    background: v.$blanco;
    font-size: 8rem;
    justify-content: space-around;
    font-size: 2rem;

    .certificado {
      width: auto;
      height: 7rem;
      margin: 0 1rem;
    }
  }
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 6rem;
  padding: 5rem;

  @include m.telefono {
    gap: 5rem;
    padding: 1rem;
    margin-top: 2rem;
  }

  @include m.tablet {
    gap: 8rem;
    padding: 1rem;
    margin-top: 4rem;
  }

  .card {
    overflow: visible;
    width: 19rem;
    height: 25.4rem;

    &:hover .content {
      transform: rotateY(180deg);
    }
  }

  .content {
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    transition: transform 300ms;
    box-shadow: 0 0 1rem 0.1rem #000000ee;
    border-radius: 0.5rem;
  }

  .back {
    width: 100%;
    height: 100%;
    justify-content: center;
    display: flex;
    align-items: center;
    overflow: hidden;

    &:before {
      position: absolute;
      content: " ";
      display: block;
      width: 50rem;
      height: 160%;
      background: rgb(201, 93, 5);
      background: linear-gradient(
        90deg,
        rgba(201, 93, 5, 1) 4%,
        rgba(255, 177, 53, 1) 37%,
        rgba(255, 152, 14, 1) 60%,
        rgba(246, 125, 4, 1) 81%,
        rgba(201, 93, 5, 1) 100%
      );
      animation: rotation_481 5000ms infinite linear;
    }
  }
  .back-content {
    position: absolute;
    width: 99%;
    height: 99%;
    background-color: v.$negro;
    border-radius: 0.5rem;
    color: v.$blanco;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3rem;
  }

  .front {
    transform: rotateY(180deg);
    color: white;
    .front-content {
      position: absolute;
      width: 100%;
      height: 100%;
      padding: 1rem;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    .img {
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  .description {
    box-shadow: 0 0 1rem 0.5rem #00000088;
    width: 100%;
    padding: 1rem;
    background-color: #00000099;
    backdrop-filter: blur(0.5rem);
    border-radius: 0.5rem;

    p {
      text-align: left;
      font-size: 1.5rem;
      margin: 0;
    }
  }

  .title {
    font-size: 1.1rem;
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;

    h3 {
      width: 100%;
      font-size: 1.45rem;
      margin-bottom: 0;
    }
  }

  .circle {
    width: 9rem;
    height: 9rem;
    border-radius: 50%;
    background-color: v.$sorbus400;
    position: relative;
    filter: blur(1.5rem);
    animation: floating 2600ms infinite linear;
  }

  .icon-indv {
    height: 10rem;
    width: 10rem;
  }
}

.front,
.back {
  background-color: v.$negro;
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  border-radius: 0.5rem;
  overflow: hidden;
}

@keyframes rotation_481 {
  0% {
    transform: rotateZ(0deg);
  }

  0% {
    transform: rotateZ(360deg);
  }
}

#bottom {
  background-color: v.$sorbus600;
  left: 5rem;
  top: 0rem;
  width: 15rem;
  height: 15rem;
  animation-delay: -800ms;
}

#right {
  background-color: v.$sorbus100;
  left: 16rem;
  top: -8rem;
  width: 3rem;
  height: 3rem;
  animation-delay: -1800ms;
}

@keyframes floating {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(0);
  }
}
